:root {
  --color-black: #000;
  --color-light: #fff;
  --color-gray: #424551;
  --color-storm: #787a80;
  --color-ebony: #1e212c;
  --color-primary: #ff5a30;
  --color-athens: #f4f5f7;
  --color-active: #fc3300;
  --grid-gap: 30px;
  --hero-slider-speed: 500;
  --main-shadow:
    0 80px 80px -20px rgba(154, 156, 165, 0.08),
    0 30px 24px -10px rgba(154, 156, 165, 0.05),
    0 12px 10px -6px rgba(154, 156, 165, 0.04),
    0 4px 4px -4px rgba(30, 33, 44, 0.03);
}

/* stylelint-disable */ /* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable value-keyword-case */
@font-face {
  font-family: "Ubuntu";
  src: url("../fonts/../fonts/UbuntuRegular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Ubuntu";
  src: url("../fonts/../fonts/UbuntuBold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/../fonts/LatoBold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Lato";
  src: url("../fonts/../fonts/LatoBlack.woff2") format("woff2");
  font-weight: 900;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Ubuntu", sans-serif;
  font-weight: 400;
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.centered {
  width: 100%;
  text-align: center;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1260px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.main-title {
  margin: 0;
  margin-bottom: 25px;
  font-weight: 700;
  font-size: 72px;
  line-height: 130%;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: var(--color-ebony);
}
.main-title span {
  color: var(--color-primary);
}
.main-title--home {
  color: var(--color-light);
}

.main-descr {
  margin: 0;
  font-size: 20px;
  line-height: 150%;
  color: var(--color-storm);
}
.main-descr--home {
  margin-bottom: 60px;
  color: var(--color-light);
}

.btn {
  display: inline-block;
  border-width: 1px;
  border-style: solid;
  border-color: transparent;
  border-radius: 4px;
  padding: 15px 39px;
  font-weight: 700;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0.5px;
  text-transform: uppercase;
  color: var(--color-light);
  transition: background-color 0.3s ease-in-out, border-color 0.3s ease-in-out;
}
.btn--fill {
  background-color: var(--color-primary);
}
.btn--fill:focus {
  outline: 2px solid var(--color-primary);
  outline-offset: 2px;
}
.btn--fill:hover {
  background-color: var(--color-active);
}
.btn--fill:active {
  background-color: var(--color-active);
}
.btn--stroke-light {
  border-color: var(--color-light);
  color: var(--color-light);
}
.btn--stroke-primary {
  border-color: var(--color-primary);
  color: var(--color-primary);
}
.btn--stroke:focus {
  outline: 2px solid var(--color-primary);
  outline-offset: 2px;
}
.btn--stroke:hover {
  border-color: var(--color-primary);
  background-color: var(--color-primary);
}
.btn--stroke:active {
  border-color: var(--color-primary);
  background-color: var(--color-active);
}

.title {
  margin: 0;
  margin-bottom: 25px;
  font-weight: 700;
  font-size: 46px;
  line-height: 130%;
  color: var(--color-ebony);
}
.title--center {
  text-align: center;
}

.descr {
  margin: 0;
  margin-bottom: 60px;
  font-size: 18px;
  line-height: 150%;
  color: var(--color-storm);
}
.descr--center {
  text-align: center;
}

.section-offset {
  padding-top: 120px;
}

.custom-checkbox__input {
  position: absolute;
  opacity: 0;
}
.custom-checkbox__text {
  position: relative;
  display: inline-block;
  padding-left: 27px;
  font-size: 14px;
  line-height: 150%;
  color: var(--color-gray);
}
.custom-checkbox__text::before {
  content: "";
  position: absolute;
  left: -1px;
  top: 1px;
  display: inline-block;
  border: 1px solid var(--color-primary);
  border-radius: 3px;
  width: 16px;
  height: 16px;
  transition: background-color 0.3s ease-in-out;
}
.custom-checkbox__text::after {
  content: "";
  position: absolute;
  left: -1px;
  top: 1px;
  width: 16px;
  height: 16px;
  background-image: url("../img/check.svg");
  background-position: center;
  background-size: 10px 8px;
  background-repeat: no-repeat;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}
.custom-checkbox__input:checked + .custom-checkbox__text::before {
  background-color: var(--color-primary);
}
.custom-checkbox__input:checked + .custom-checkbox__text::after {
  opacity: 1;
}
.custom-checkbox__input:focus + .custom-checkbox__text {
  outline: 3px solid var(--color-primary);
}

.custom-file {
  cursor: pointer;
}
.custom-file__input {
  position: absolute;
  opacity: 0;
}
.custom-file__text {
  position: relative;
  display: inline-block;
  padding-left: 36px;
  font-size: 14px;
  line-height: 170%;
  color: var(--color-gray);
  background-image: url("../img/file-icon.svg");
  background-position: left center;
  background-size: 24px 24px;
  background-repeat: no-repeat;
}
.custom-file__input:focus + .custom-file__text {
  outline: 3px solid var(--color-primary);
}

.custom-radio__input {
  position: absolute;
  opacity: 0;
}
.custom-radio__text {
  position: relative;
  display: inline-block;
  padding-left: 27px;
  font-size: 16px;
  line-height: 160%;
  color: var(--color-gray);
}
.custom-radio__text::before {
  content: "";
  position: absolute;
  left: -1px;
  top: 4px;
  display: inline-block;
  border: 1px solid #b3b7b7;
  border-radius: 100%;
  width: 16px;
  height: 16px;
  transition: border-color 0.3s ease-in-out;
}
.custom-radio__text::after {
  content: "";
  position: absolute;
  left: -1px;
  top: 4px;
  width: 16px;
  height: 16px;
  background-image: url("../img/check-circle.svg");
  background-position: center;
  background-size: 8px 8px;
  background-repeat: no-repeat;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}
.custom-radio__input:checked + .custom-radio__text::before {
  border-color: var(--color-primary);
}
.custom-radio__input:checked + .custom-radio__text::after {
  opacity: 1;
}
.custom-radio__input:focus + .custom-radio__text {
  outline: 3px solid var(--color-primary);
}

.grid {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  gap: var(--grid-gap);
}

.to-top {
  position: fixed;
  right: calc((100% - 1230px) / 2);
  bottom: 80px;
  z-index: 1000;
  display: flex;
  align-items: flex-end;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}
.to-top__text {
  margin-right: 16px;
  font-size: 14px;
  line-height: 150%;
  text-shadow: 3px 3px 11px rgb(150, 150, 150);
  text-transform: uppercase;
  color: var(--color-light);
}
.to-top__icon {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  width: 40px;
  height: 40px;
  background: var(--color-primary);
  transition: background-color 0.3s ease-in-out;
}
.to-top__icon svg {
  width: 24px;
  height: 24px;
  object-fit: cover;
}
.to-top__icon:hover {
  background-color: var(--color-active);
}
.to-top--active {
  opacity: 1;
  visibility: visible;
}
@media (max-width: 1440px) {
  .to-top {
    right: 20px;
    bottom: 20px;
  }
}

.site-modal {
  border-radius: 4px;
  padding: 48px;
  width: 486px;
}
.site-modal .graph-modal__close {
  right: 24px;
  top: 24px;
  width: 24px;
  height: 24px;
  background-image: url("../img/close-modal.svg");
}
.site-modal__title {
  margin: 0;
  margin-bottom: 24px;
  font-weight: 700;
  font-size: 28px;
  line-height: 150%;
  text-align: center;
  color: var(--color-ebony);
}
.site-modal__field {
  margin-bottom: 20px;
}
.site-modal__btn {
  margin-top: 20px;
  width: 100%;
}
.site-modal .choices {
  width: 100%;
}
.site-modal .is-focused {
  outline: 2px solid var(--color-primary);
  outline-offset: 2px;
}
.site-modal .choices__inner {
  margin: 0;
  border: 1px solid #d7dadd;
  border-radius: 4px;
  padding: 0;
  height: 52px;
  background-color: var(--color-athens);
}
.site-modal .choices__list--single .choices__placeholder {
  font-size: 16px;
  line-height: 160%;
  color: #9a9ca5;
  opacity: 1;
}
.site-modal .choices[data-type*=select-one] .choices__inner {
  padding-bottom: 0;
}
.site-modal .choices__list--single {
  display: flex;
  align-items: center;
  padding-left: 15px;
  height: 100%;
}
.site-modal .choices[data-type*=select-one]::after {
  right: 19px;
  margin-top: 0;
  border: none;
  width: 14px;
  height: 7px;
  background-image: url("../img/angle-down.svg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  transform: translateY(-50%);
  transition: transform 0.3s ease-in-out;
}
.site-modal .choices[data-type*=select-one].is-open::after {
  margin-top: -7px;
  transform: rotate(180deg) translateY(-50%);
}
.site-modal .choices__list--dropdown .choices__item--selectable::after {
  display: none;
}
.site-modal .choices__list--dropdown {
  background-color: var(--color-athens);
}

.slider-nav {
  display: flex;
  align-items: center;
  position: relative;
  z-index: 15;
  user-select: none;
}
.slider-nav__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  width: 48px;
  height: 48px;
  color: var(--color-gray);
  transition: background-color 0.3s ease-in-out, color 0.3s ease-in-out;
}
.slider-nav__btn svg {
  display: block;
  width: 24px;
  height: 24px;
  color: inherit;
  fill: currentColor;
  object-fit: cover;
}
.slider-nav__btn:focus {
  outline: 2px solid var(--color-primary);
  outline-offset: 2px;
}
.slider-nav__btn:hover {
  color: var(--color-light);
  background-color: var(--color-primary);
}
.slider-nav__btn:active {
  color: var(--color-light);
  background-color: var(--color-active);
}

.slider-nav__btn.swiper-button-disabled {
  opacity: 0.4;
  pointer-events: none;
}

.form-field {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.form-field__caption {
  margin-bottom: 7px;
  font-size: 14px;
  line-height: 150%;
  color: var(--color-gray);
}
.form-field__input {
  border: 1px solid #d7dadd;
  border-radius: 4px;
  padding-left: 15px;
  width: 260px;
  height: 44px;
  font-size: 14px;
  line-height: 150%;
}
.form-field__input::placeholder {
  color: #9a9ca5;
}
.form-field__input:focus {
  outline: 2px solid var(--color-primary);
  outline-offset: 2px;
}
.form-field__input--big {
  padding-top: 10px;
  height: 66px;
  resize: none;
}
.form-field--big {
  flex-grow: 1;
}
.form-field--big .form-field__input {
  width: 100%;
}
.form-field--gray {
  width: 100%;
}
.form-field--gray .form-field__input {
  width: 100%;
  background-color: var(--color-athens);
}

.social {
  display: flex;
  align-items: center;
}
.social__item:not(:last-child) {
  margin-right: 16px;
}
.social__link {
  display: block;
  width: 24px;
  height: 24px;
}
.social__link svg {
  display: block;
  width: 100%;
  height: 100%;
  fill: var(--color-light);
  object-fit: cover;
  opacity: 0.6;
  transition: opacity 0.3s ease-in-out;
}
.social__link:focus {
  outline: 2px solid var(--color-primary);
  outline-offset: 2px;
}
.social__link:hover svg {
  opacity: 1;
}
.social__link:active svg {
  opacity: 1;
}

.header {
  padding-top: 23px;
  padding-bottom: 21px;
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__nav {
  margin-left: -112px;
}
@media (max-width: 1200px) {
  .header__nav {
    margin-left: 0;
  }
}
.header--main {
  background-color: var(--color-light);
}
.header--secondary {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
}

@media (max-width: 1200px) {
  .logo {
    max-width: 100px;
  }
}
.logo:focus {
  outline: 2px solid var(--color-primary);
  outline-offset: 2px;
}

.hero__content {
  max-width: 590px;
}
.hero__next {
  position: absolute;
  right: 32px;
  top: 50%;
  z-index: 30;
  background-color: var(--color-light);
  transform: translateY(-50%);
}
@media (max-width: 1440px) {
  .hero__next {
    display: none;
  }
}
.hero__prev {
  position: absolute;
  left: 32px;
  top: 50%;
  z-index: 30;
  background-color: var(--color-light);
  transform: translateY(-50%);
}
@media (max-width: 1440px) {
  .hero__prev {
    display: none;
  }
}
.hero__pag {
  counter-reset: pag;
  left: calc((100% - 1260px) / 2) !important;
  bottom: 120px !important;
  display: flex;
}
.hero__pag .swiper-pagination-bullet {
  display: flex;
  flex-direction: column;
  border-radius: 0;
  width: 178px;
  height: auto;
  text-align: left;
  background-color: transparent;
  opacity: 1;
}
.hero__pag .swiper-pagination-bullet::before {
  counter-increment: pag;
  content: "0" counter(pag);
  font-weight: 700;
  font-size: 28px;
  line-height: 150%;
  color: rgba(255, 255, 255, 0.3);
  transition: color 0.3s ease-in-out;
}
.hero__pag .swiper-pagination-bullet::after {
  display: none;
}
.hero__bar {
  position: relative;
  overflow: hidden;
  border-radius: 100px;
  width: 100%;
  height: 2px;
  background-color: rgba(255, 255, 255, 0.3);
}
.hero__bar::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: var(--color-light);
  transform: scaleX(0);
  transform-origin: left;
}
.hero__btns .btn:not(:last-child) {
  margin-right: 20px;
}

.swiper-pagination-bullet-active::before {
  color: var(--color-light) !important;
}
.swiper-pagination-bullet-active .hero__bar::after {
  transform: scaleX(1);
  transition: transform var(--hero-slider-speed) ease-in-out;
}

.hero-slider {
  position: relative;
  width: 100%;
  overflow: hidden;
}

.hero-slide {
  padding-top: 220px;
  padding-bottom: 354px;
  background-image: url("../img/hero-bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: var(--color-gray);
}

.about-section {
  padding-bottom: 60px;
  background-color: var(--color-light);
}

.mission {
  background-color: var(--color-light);
}
.mission__list {
  --mission-gap: 94px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin-bottom: 27px;
  gap: calc(var(--mission-gap) * 2);
}
.mission__item {
  position: relative;
  padding-top: 71px;
  text-align: center;
  background-position: top center;
  background-size: 48px;
  background-repeat: no-repeat;
}
.mission__item:not(:last-child)::after {
  content: "";
  position: absolute;
  right: calc(-1 * var(--mission-gap));
  top: 0;
  width: 1px;
  height: 100%;
  background-image: url("../img/divider.svg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.mission__item--quality {
  background-image: url("../img/quality.svg");
}
.mission__item--safety {
  background-image: url("../img/safety.svg");
}
.mission__item--comfort {
  background-image: url("../img/comfort.svg");
}
.mission__subtitle {
  margin: 0;
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 20px;
  line-height: 150%;
  color: var(--color-ebony);
}
.mission__descr {
  margin: 0;
  font-size: 16px;
  line-height: 160%;
  color: var(--color-storm);
}

.know-form {
  position: relative;
  top: 92px;
  border-radius: 4px;
  padding: 48px 60px;
  box-shadow: 0 80px 80px -20px rgba(154, 156, 165, 0.08), 0 30px 24px -10px rgba(154, 156, 165, 0.05), 0 12px 10px -6px rgba(154, 156, 165, 0.04), 0 4px 4px -4px rgba(30, 33, 44, 0.03);
  background-color: var(--color-light);
}
.know-form__title {
  margin: 0;
  margin-bottom: 33px;
  font-weight: 700;
  font-size: 28px;
  line-height: 150%;
  text-align: center;
  color: var(--color-ebony);
}
.know-form__content {
  display: flex;
  align-items: flex-end;
}
.know-form__field {
  margin-right: 24px;
}
.know-form__btn {
  padding: 11px 29px;
}

.services-section {
  padding-top: 212px;
  padding-bottom: 80px;
  background-image: url("../img/services-bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.services-section__list {
  margin-bottom: 80px;
}

.services-list__item {
  grid-column: 3 span;
}

.services-item {
  position: relative;
  display: block;
  border: 1px solid #e5e8ed;
  border-radius: 4px;
  padding: 0 10px;
  padding-top: 87px;
  padding-bottom: 90px;
  box-shadow: var(--main-shadow);
  background: var(--color-light);
}
.services-item::before {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 3;
  border-radius: inherit;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-color: var(--color-ebony);
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}
.services-item::after {
  content: "";
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 5;
  border-radius: inherit;
  background-color: rgba(30, 33, 44, 0.6);
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}
.services-item:focus {
  outline: 2px solid var(--color-primary);
  outline-offset: 2px;
}
.services-item__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  z-index: 10;
}
.services-item__icon {
  margin-bottom: 23px;
  width: 52px;
  height: 52px;
  fill: var(--color-primary);
  object-fit: cover;
  transition: fill 0.3s ease-in-out;
}
.services-item__title {
  margin: 0;
  font-weight: 700;
  font-size: 20px;
  line-height: 150%;
  text-align: center;
  color: var(--color-ebony);
  transition: color 0.3s ease-in-out;
}
@media (max-width: 1200px) {
  .services-item__title {
    font-size: 18px;
  }
}
.services-item__construct::before {
  background-image: url("../img/services-1.jpg");
}
.services-item:hover::before {
  opacity: 1;
}
.services-item:hover::after {
  opacity: 1;
}
.services-item:hover .services-item__icon {
  fill: var(--color-light);
}
.services-item:hover .services-item__title {
  color: var(--color-light);
}

.portfolio-section {
  padding: 120px 0;
  background-color: var(--color-light);
}
