.team {
    padding-top: 54px;
    padding-bottom: 120px;

    &__list {
        column-gap: var(--grid-gap);
        margin-bottom: 80px;
        row-gap: 64px;
    }

    &__top {
        position: relative;
        margin-bottom: 12px;
        font-size: 0;
    }

    &__socials {
        position: absolute;
        left: 0;
        bottom: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 0 0 4px 4px;
        padding: 20px 10px;
        width: 100%;
        background-color: rgba(#ff5a30, 0.7);
        opacity: 0;
        visibility: hidden;
        transition: opacity 0.3s ease-in-out, visibility 0.3 ease-in-out;
    }

    &__item {
        grid-column: 3 span;

        &:hover {
            .team__socials {
                opacity: 1;
                visibility: visible;
            }
        }
    }

    &__text {
        font-size: 18px;
        line-height: 150%;
        text-align: center;
        color: var(--color-ebony);

        a {
            border-bottom: 1px solid currentColor;
            color: var(--color-primary);
            transition: color 0.3s ease-in-out;

            &:focus {
                outline: 2px solid var(--color-primary);
                outline-offset: 2px;
            }
        
            &:hover {
                color: var(--color-active);
            }
        
            &:active {
                    color: var(--color-active);
            }
        }
    }

    &__link {
        display: block;
        height: 20px;
        width: 20px;

        svg {
            display: block;
            width: 100%;
            height: 100%;
            fill: var(--color-light);
            object-fit: cover;
        }

        &:not(:last-child) {
            margin-right: 12px;
        }
    }

    &__name {
        margin-bottom: 5px;
        font-weight: 700;
        font-size: 20px;
        line-height: 150%;
        text-align: center;
        color: var(--color-gray);
    }

    &__post {
        font-size: 16px;
        line-height: 160%;
        text-align: center;
        color: var(--color-storm);
    }
}